$accent-color-dark: rgba(108, 91, 117, 0.8);
$accent-color-light: rgba(242,136,79, 0.8);
@import url('https://fonts.googleapis.com/css?family=IBM+Plex+Mono:300,400,500,700&display=swap');

* {
  box-sizing: border-box;
  font-family: 'IBM Plex Mono', monospace;
  margin: 0;
  padding: 0
}

.App {
  background: url(../Images/tatooine_background.jpg); 
  background-size: cover;
  background-repeat: no-repeat
}

.loading-img {
  background: url(../Images/loadingImg.gif); 
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  justify-content: center;
  height:100vh;
  width:100vw;
}

