  height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50%; }
  .form-background label {
    font-size: 18px; }
  .form-background input {
    margin: 10px;
    border-radius: 5px;
    width: 50%;
    padding: 10px; }
    .form-background input::-webkit-input-placeholder {
      font-size: 16px; }
    .form-background input::-moz-placeholder {
      font-size: 16px; }
    .form-background input:-ms-input-placeholder {
      font-size: 16px; }
    .form-background input::-ms-input-placeholder {
      font-size: 16px; }
    .form-background input::placeholder {
      font-size: 16px; }
  .form-background select {
