    nav a:hover {
      cursor: pointer;
      color: rgba(242, 136, 79, 0.8); }
    nav a a:active {
      color: rgba(242, 136, 79, 0.8); }
  nav button {
    background-color: rgba(242, 136, 79, 0.8);
    border-radius: 5px;
    color: #ffffff;
    font-size: 20px;
    height: 40px;
    margin-right: 20px;
    width: 125px; }
