.inner {
  background: rgba(242, 136, 79, 0.8);
  border-radius: 10px;
  -webkit-clip-path: circle(7% at 10% 30%);
          clip-path: circle(7% at 10% 30%);
  cursor: pointer;
  margin-top: 10px;
  margin-top: 50px;
  padding: 1em;
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
  width: 500px; }
  .inner:hover {
    background: #e76551;
    -webkit-clip-path: circle(75%);
            clip-path: circle(75%); }
  .inner h1 {
    color: white;
    margin: 0; }
  .inner p {
    color: white;
    font-size: 1.15rem;
    margin-left: 25%; }
  .inner span {
    float: left;
    color: white;
    float: left;
    font-weight: bold;
    height: 10px;
    margin-left: 29px;
    margin-top: 5px;
    position: relative;
    -webkit-transition: color .5s;
    transition: color .5s;
    width: 10px; }
  .inner:hover span {
    color: #e76551; }

.scroll {
  background: url(/swapi-trivia/static/media/spaceImg.2b2bc624.jpg);
  height: 100%;
  overflow: hidden;
  width: 100%; }

.fade {
  position: relative;
  width: 100%;
  min-height: 60vh;
  top: -25px;
  background-image: -webkit-gradient(linear, left bottom, left top, from(transparent), color-stop(75%, black));
  background-image: linear-gradient(0deg, transparent, black 75%);
  z-index: 1; }

.star-wars {
  display: flex;
  justify-content: center;
  position: relative;
  height: 800px;
  color: #feda4a;
  font-size: 500%;
  font-weight: 600;
  line-height: 150%;
  -webkit-perspective: 400px;
          perspective: 400px;
  text-align: justify; }

.crawl {
  position: relative;
  top: 99999px;
  -webkit-transform-origin: 50% 100%;
          transform-origin: 50% 100%;
  -webkit-animation: crawl 60s linear infinite;
          animation: crawl 60s linear infinite; }

.crawl > .title {
  font-size: 90%;
  text-align: center; }

.crawl > .title h1 {
  margin: 0 0 100px;
  text-transform: uppercase; }

@-webkit-keyframes crawl {
  0% {
    top: -100px;
    -webkit-transform: rotateX(20deg) translateZ(0);
            transform: rotateX(20deg) translateZ(0); }
  100% {
    top: -6000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px); } }

@keyframes crawl {
  0% {
    top: -100px;
    -webkit-transform: rotateX(20deg) translateZ(0);
            transform: rotateX(20deg) translateZ(0); }
  100% {
    top: -6000px;
    -webkit-transform: rotateX(25deg) translateZ(-2500px);
            transform: rotateX(25deg) translateZ(-2500px); } }

